	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/secret-weapon-2-smashing-magazine/index.md absPath of file >>> MarkdownRemark","html":"<p><img src=\"/content/images/2007/05/logo.gif\" alt=\"Smashing Magazine Logo\"></p>\n<p><a href=\"http://www.smashingmagazine.com/\" title=\"Open Smashing Magazine in a new window\">Smashing magazine</a> <em>('we smash you with the information, which will make your life easier. really.')</em> is one of those pseudo-blogs, regurgitating content from other sites as a method of generating traffic. Well, at least that's what I assume. Behind the ads* there's a wealth of decent resources, and it's updated at a furious rate. For example, there seems to be 100 links+ in each of their '<a href=\"http://www.smashingmagazine.com/category/digest/\" title=\"Open Smashing Magazine&#x27;s Best of Month Articles in a new window\">Best of Month</a>' articles. (and with that many links, there's gotta be <strong>something good</strong>)</p>\n<p>They don't obviously write all this content, but seem to read and research everything that's going on through <a href=\"http://www.digg.com\" title=\"Open digg.com in a new window\">digg</a>, <a href=\"http://www.digital-web.com\" title=\"Open Digital Web in a new window\">digital web</a> and the like, and then prepare and serve it up in healthy doses every week (or more).</p>\n<p>Head on over there, read their <a href=\"http://www.smashingmagazine.com/\" title=\"Open Smashing Magazine in a new window\">past articles</a> and sign up to <a href=\"http://www.smashingmagazine.com/wp-rss.php\" title=\"Smashing Magazine&#x27;s RSS feed\">their RSS</a>. You'll thank me later on.</p>\n<p>* remind me to do a post on how to remove them sometime...</p>","frontmatter":{"title":"Keeping your finger on the pulse with Smashing Magazine","date":"May 16, 2007","tags":["design","external-links","magazines","resources"]}}},"pathContext":{}}

